@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Roboto+Mono:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bree+Serif&family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bree+Serif&family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bree+Serif&family=Roboto+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bree+Serif&family=Roboto+Mono:wght@700&family=Roboto:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=Bree+Serif&family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.header{
    background: #fff;
    margin-top: 0px;
    height: 60px;
    align-items: center;
    max-height: 70px;
    box-shadow: 0px 4px 12px 1px rgb(202, 180, 180);
    width: 100%;
}
.header .headerLinks{
    margin: auto;
    max-width: 1200px;
    margin-top: 9px;
}
.headerRow{
    display: flex;
    margin: auto;
}
.headerRow Col{
    align-items: center;
    justify-content: left;
}
.headerRow .menuLinks{
    display: flex;
    align-items: center;
}
.headerRow h1{
    font-size: 30px;
    font-family: 'Audiowide', cursive;
    color: #a20a0a;
    text-align: left;
    align-items: center;
}
.headerRow h1 span{
    color: #444;
}
.headerRow ul{
    list-style: none;
    display: flex;
    margin: 0px;
    padding: 0px;
}
.headerRow ul li{
    text-align: left;
    margin: 15px;
    margin-left: 0px;
}
.headerRow ul li a{
    color: #888;
    font-family: 'Roboto Mono', monospace;
    font-size: 13px;
    text-decoration: none;
}
.headerRow ul li a.active{
    color: #a20a0a;
}
.headerRow ul li a:hover{
    color: #a20a0a;
}
.orderTrack{
    margin-left: 40px;
}
.menuIcons{
    font-size: 14px;
    margin: 13px;
}
.homeRow{
    max-width: 1200px;
    margin: auto;
    min-height: 500px;
    align-items: center;
    text-align: left;
}
.homeRow h1{
    font-size: 45px;
    font-family: 'Bree Serif', serif;
    max-width: 300px;
    color: #333;
}
.homeRow p{
    font-size: 11px;
    font-family: 'Roboto Mono', monospace;
    color: #666;
    max-width: 300px;
    margin-top: 10px;
}
.homeRow button{
    font-family: 'Bree Serif', serif;
    padding: 10px;
    width: 150px;
    font-size: 14px;
    border-radius: 24px;
    border-width: 0px;
    background: #a20a0a;
    outline: none;
    color: #fff;
    margin-top: 10px;
}
.homeRow .homeCover{
    background-image: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1604484609/pexels-alexy-almond-3756523_zewvna.jpg');
    background-position: center;
    background-size: cover;
    height: 550px;
    z-index: -1;
}

/* lower search filter */
.navSearch{
    max-width: 1000px;
    margin: auto;
    display: flex;
    background: #fff;
    height: 60px;
    justify-content: space-between;
    margin-top: -100px;
    align-items: center;
}
.navSearch .search{
    border: 1px solid rgb(150, 150, 150, 0.4);
    padding: 0px;
    margin: 0px;
    border-radius: 20px;
    height: 35px;
    align-items: center;
    width: 300px;
    display: flex;
    justify-content: space-between;
}
.navSearch .search input{
    padding: 5px;
    margin-left: 10px;
    border: none;
    outline: none;
    width: 250px;
}
.navSearch .search input::-webkit-input-placeholder{
    color: rgb(160, 160, 160,0.5);
}
.navSearch .search input:-ms-input-placeholder{
    color: rgb(160, 160, 160,0.5);
}
.navSearch .search input::-ms-input-placeholder{
    color: rgb(160, 160, 160,0.5);
}
.navSearch .search input::placeholder{
    color: rgb(160, 160, 160,0.5);
}
.navSearch .search .searchOptionsIcon{
    margin-right: 10px;
    color: rgb(160, 160, 160,0.5);
    font-size: 17px;
}
.navSearch h2{
    font-size: 14px;
    margin: 10px 20px;
    margin-left: 0px;
    font-weight: 500;
}
.navSearch ul{
    list-style: none;
    display: flex;
    margin: 0px;
    margin-left: -60px;
    padding: 0px;
}
.navSearch ul li{
    font-size: 13px;
    margin: 10px 8px;
    padding: 4px 10px;
    font-family: 'Roboto Mono', monospace;
    color: rgb(107, 107, 107, 0.6);
    border: 1px solid  rgb(107, 107, 107, 0.6);
    border-radius: 17px;
}
.navSearch ul li.active{
    border: 2px solid #111;
    border-radius: 17px;
    color: #111;
    font-weight: 600;
}
.navSearch .moreOption{
    background: #a20a0a;
    padding: 12px 15px;
    margin-right: 10px;
    border-radius: 10px;
}
.navSearch .moreOptions{
    border-radius: 15px;
    color: #fff;
    font-size: 10px;
}
.featured{
    background: #f9f9f9;
}
.featuredRow{
    max-width: 1100px;
    margin: auto;
    padding: 40px 0px;
    margin-top: 100px;
    display: flex;
    flex-wrap: wrap;
}
.featuredRow h1{
    font-size: 30px;
    font-family: 'Bree Serif', serif;
    color: #333;
    margin-top: 40px;
    margin-left: 25px;
}
.featuredRow h3{
    font-size: 14px;
    color: #444;
    font-weight: 700;
    margin: 12px;
    margin-left: 25px;
}
.featuredRow p{
    font-size: 10.5px;
    margin: 12px;
    margin-left: 25px;
    color: #999;
    font-weight: 600;
}
.featuredRow h5{
    font-size: 11px;
    margin: 12px;
    margin-left: 25px;
    color: #777;
    letter-spacing: 0.5px;
    margin-right: 25px;
}
.featuredRow .featuredCol{
    width: 100%;
    overflow: hidden;
    border: 1px solid rgb(179, 179, 179, 0.1);
    margin: 10px;
    border-radius: 10px;
    padding: 0px;
    height: 360px;
    text-align: left;
    background: #fff;
}
.featuredRow .featuredCol img{
    width: 100%;
    margin: 0px;
    height: 180px;
}
.featuredRow .specialFeatured{
    width: 100%;
    border: 1px solid rgb(179, 179, 179, 0.1);
    margin: 10px;
    border-radius: 10px;
    padding: 0px;
    height: 360px;
    background: #fff;
    overflow: hidden;
}
.featuredRow .specialFeatured img{
    width: 100%;
    height: 100%;
}
.featuredRow .specialFeature{
    width: 100%;
    border: 1px solid rgb(179, 179, 179, 0.1);
    margin: 10px;
    background: #fff;
    border-radius: 10px;
    padding: 0px;
    height: 360px;
    overflow: hidden;
    text-align: left;
}
.featuredRow .specialFeature .specialImage{
    margin-left: 25px;
    display: flex;
}
.specialFeature img{
    width: 85px;
    height: 70px;
    margin: 5px;
    margin-left: 0px;
    margin-right: 10px;
    border-radius: 10px;
}
.featuredRow .exploreSpecial{
    display: flex;
}
.featuredRow .featuredCol:hover, .featuredRow .specialFeature:hover, .featuredRow .specialFeatured:hover{
    box-shadow: 0px 0px 30px 10px rgb(206, 206, 206, 0.4);
}
.featuredRow .exploreSpecial button{
    padding: 8px 20px;
    border-radius: 20px;
    border: 1px solid rgb(179, 179, 179, 0.7);
    margin-left: 25px;
    outline: none;
    font-size: 13px;
    margin-top: 2px;
}
.featuredRow .exploreSpecial .exploreMore{
    padding: 6px 10px;
    border: 1px solid rgb(179, 179, 179, 0.7);
    margin-top: 2px;
    border-radius: 20px;
    margin-left: 15px;
}
.featuredRow .postShare{
    display: flex;
    border-top: 1px solid rgb(179, 179, 179, 0.7);
    margin-top: 27px;
}
.featuredRow .postShare .features{
    display: flex;
    justify-content: space-around;
}
.featuredRow .postShare .featureAction{
    display: flex;
    margin-right: 10px;
}
.featuredRow .postShare .featureSave, .featuredRow .postShare .featureLike, .featuredRow .postShare .featureComment{
    margin: 6px;
    font-size: 20px;
    color: rgb(146, 146, 146);
}
.featuredRow .postShare h6{
    margin: 6px;
    margin-left: 0px;
    color: rgb(146, 146, 146);
    font-size: 12px;
}
.process{
    background: #f0f0f0;
    padding-bottom: 5px;
    margin: 0px;
}
.processRow{
    max-width: 1200px;
    margin: auto;
    padding: 50px 0px;
}
.processCol{
    border: 1px solid rgb(245, 245, 245);
    height: 300px;
    border-radius: 10px;
    background: #fff;
    text-align: center;
    box-shadow: 0px 0px 25px 4px #ddd;
}
.processNumber{
    text-align: left;
    position: absolute;
}
.processCol h2{
    margin: 10px;
    padding: 6px 11px;
    background: #a20a0a;
    border-radius: 25px;
    font-size: 15px;
    color: #f3f3f3;
}
.processCol img{
    width: 100px;
    height: 80px;
    margin-top: 50px;
}
.processCol h1{
    font-family: 'Bree Serif', serif;
    font-size: 18px;
    margin: 13px;
    color: #666;
}
.underline{
    width: 50px;
    height: 3px;
    background: #a20a0a;
    margin: auto;
    margin-bottom: 20px;
}
.processCol p{
    max-width: 220px;
    margin: auto;
    font-size: 12px;
    font-weight: 600;
    color: #999;
    letter-spacing: 0.3px;
}

.processRow1{
    max-width: 1100px;
    margin: auto;
    background: #fff;
    align-items: center;
    text-align: left;
    box-shadow: 0px 40px 50px 5px #aaa;
    padding: 20px;
    margin-bottom: 100px;
    border-radius: 30px;
}
.processRow1 .betterExp h1{
    font-family: 'Bree Serif', serif;
    max-width: 300px;
    font-size: 33px;
    color: #333;
    margin: 20px;
}
.processRow1 .betterExp p{
    font-size: 12px;
    max-width: 300px;
    margin: 20px;
    color: #777;
}
.processRow1 .betterExp button{
    padding: 12px 45px;
    background: #a20a0a;
    border-width: 0px;
    color: #fff;
    margin: 10px 20px;
    border-radius: 22px;
}
.processRow1 .mobileImage img{
    width: 100%;
    margin: 20px;
}
.benefitsPoint{
    display: flex;
}
.benefitsPoint .point{
    width: 30px;
    height: 10px;
    margin: 20px;
    border-radius: 10px;
    background: #a20a0a;
}
.benefitsPoint h1{
    font-size: 16px;
    font-family: 'Bree Serif', serif;
    color: #333;
    margin: 15px;
}
.benefitsPoint h6{
    font-size: 11px;
    margin: 10px 15px;
    font-weight: 600;
    color: #777;
}
.review{
    margin: 0px;
    padding: 0px;
}
.coupon{
    background-image: url('https://res.cloudinary.com/dmixz7eur/image/upload/v1604630130/Web_1920_1_q6hlyy.jpg');
    background-position: center;
    background-size: cover;
    margin: 0px;
    max-width: 1440px;
    margin: auto;
}
.coupon h1{
    font-size: 50px;
    font-family: 'Bree Serif', serif;
    padding: 60px 20px;
    color: #f1f1f1;
}
.coupon p{
    font-size: 11.2px;
    max-width: 560px;
    margin: 20px;
    margin: auto;
    color: rgba(241, 241, 241, 0.8);
    font-weight: 500;
}
.couponRow {
    align-items: center;
    justify-content: center;
    margin: 0px;
    padding: 0px;
}
.couponRow .couponCol{
    max-width: 250px;
    margin: 0px;
    padding: 30px 0px;
}
.couponRow .input{
    border: none;
    border-radius: 22px;
    margin: 40px 20px;
