    background: none;
    outline: none;
}
.couponRow .input .userIcon{
    margin: 10px;
    color: rgb(109, 109, 109, 0.8);
}
.couponRow input::-webkit-input-placeholder{
    color: #666;
}
.couponRow input:-ms-input-placeholder{
    color: #666;
}
.couponRow input::-ms-input-placeholder{
    color: #666;
}
.couponRow input::placeholder{
    color: #666;
}
.couponRow button{
    padding: 12px;
    width: 220px;
    background: #1b1616;
    box-shadow: 0px 0px 10px 1px rgba(65, 0, 0, 0.6);
    border-width: 0px;
    color: #fff;
    margin: 10px 20px;
    outline: none;
    border-radius: 22px;
}
.coupon img{
    margin: 20px;
    margin-bottom: 100px;
    max-width: 1000px;
    border-radius: 30px;
    box-shadow: 0px 40px 50px 16px rgba(116, 33, 33, 0.3);
}

.chefs{
    margin: 0px;
    padding: 0px;
    text-align: left;
    max-width: 1210px;
    margin: auto;
}
.chefs h2{
    font-size: 35px;
    margin: 20px;
    color: #333;
    font-weight: 600px;
    font-family: 'Audiowide', cursive;
}
.chefs .chefsRow{
    margin: 0px;
    margin: auto;
}
.chefsRow .profile{
    display: flex;
}
.chefsRow .profile img{
    width: 60px;
    height: 60px;
    border-radius: 30px;
}
.chefs .profile .name h3{
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
    margin: 5px 10px;
}
.chefs .profile .name h6{
    font-family: 'Roboto', sans-serif;
    font-size: 10px;
