    margin: auto;
    justify-content: space-between;
}
.privacy .terms{
    display: flex;
}
.privacy .terms h5:hover{
    color: #a20a0a;
    cursor: pointer;
}
.privacy h5{
    font-size: 10px;
    margin-right: 40px;
    color: #777;
    margin-top: 20px;
    margin-bottom: 20px;
}
